export default {
    data() {
        return {
			isShow: getApp().globalData.isShow,
            inputValue: '',
			isLoad: false,
        }
    },
	onLoad() {
		this.getConfig()
	},
    methods: {
		getConfig () {
			this.isLoad = true;
			this.$api.config().then(res => {
				my.navigateToMiniProgram({
					appId: '2018122562686742',
					path: `pages/index/index?originAppId=2019101468369526&newUserTemplate=${res.activity_id || 'KP20191216000002176162'}`,
					success: (res) => {
						console.log(1, res)
					},
					fail: (res) => {}
				});
				getApp().globalData.isShow = res.is_show_jf === '1'
				this.isShow = res.is_show_jf === '1'
				if (res.is_show_jf === '1') {
					const appId = '123'
					const templateId = '123'
					this.getAuth()
					this.noticeModal()
					uni.showTabBar()
				} else {
					uni.hideTabBar()
				}
			}).catch(err => {
				console.log(err)
			})
		},
		getAuth () {
			my.getAuthCode({
				scopes: ['auth_user'],
				success: (res) => {
					this.auth(res.authCode)
				},
			});
		},
		auth (auth){
			const params = {
				'auth_code': auth
			}
			this.$api.auth(params).then(res => {
				console.log(res)
				getApp().globalData.userId = res.user_id
				if (this.inputValue) {
					this.searchInfo()
				}
			}).catch(err => {
				console.log(err)
			})
		},
		noticeModal(){
			uni.showModal({
			    title: '业务处理通知',
			    content: '订单处理周期1个工作日，加急订单60分钟（早上8：30-晚上20：30）。告知单，通知书系统无法处理。',
				showCancel: false,
				confirmText: '我知道了',
			    success: function (res) {
			        if (res.confirm) {
			            console.log('用户点击确定');
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		searchInfo (){
			if (!getApp().globalData.userId) {
				this.getAuth()
				// 		uni.showToast({
				// 			title: '请您先授权，未授权不能查询',
				// 			icon: 'none'
				// 　　　　　})
				return
			}
			try {
				const params = {
					fkdbh: this.inputValue,
					user_id: getApp().globalData.userId,
				};
				this.$api.search(params).then(res => {
					uni.navigateTo({
						url: `/pages/order/index?id=${this.inputValue}`
					});
					getApp().globalData.orderInfo = res
					console.log(res, this.inputValue)
				}).catch(err => {
					console.log(err)
				})
			} catch (e) {
				uni.showToast({
					title: '暂无数据',
					icon: 'none'
		　　　　　})
			}
		},
        onKeyInput: function(event) {
            this.inputValue = event.target.value
        }
    }
}
